let updateBetState = async function () {
  console.log('updateBetState')
  // let resBetKey = store.state.OTcontractInstance().getLastTickbyPairTF(store.state.web3.keyPairTimeFrameListSelectedcomplex, {from: store.state.web3.coinbase})
  let resBetKey = store.state.ECBcontractInstance().getLastBarbyPairTF(store.state.web3.keyPairTimeFrameListSelectedcomplex, {from: store.state.web3.coinbase})

  store.dispatch('writeBetKey', resBetKey)
  let resBetKeyState = store.state.ECBcontractInstance().GetBetState(store.state.web3.betKey, {from: store.state.web3.coinbase})
  // console.log('get betstate for betkey', store.state.web3.betKey, ' is ', resBetKeyState)
  store.dispatch('writeBetKeyState', resBetKeyState)

  // console.log('upperStakesWEIBigNumber')
  let upperStakesWEIBigNumber = new BigNumber(store.state.ECBcontractInstance().GetMyBetUpper(store.state.web3.betKey, {from: store.state.web3.coinbase})[1])
  // console.log('store.state.ECBcontractInstance().GetMyBetUpper(store.state.web3.betKey, {from: store.state.web3.coinbase})=', store.state.ECBcontractInstance().GetMyBetUpper(store.state.web3.betKey, {from: store.state.web3.coinbase})[1])
  let upperStakesWEI = upperStakesWEIBigNumber.toNumber()
  // console.log('upperStakesWEI', upperStakesWEI)
  store.dispatch('setUpperStakes', upperStakesWEI)
  let lowerStakesWEIBigNumber = new BigNumber(store.state.ECBcontractInstance().GetMyBetLower(store.state.web3.betKey, {from: store.state.web3.coinbase})[1])
  let lowerStakesWEI = lowerStakesWEIBigNumber.toNumber()
  // console.log('lowerStakesWEI', lowerStakesWEI)
  store.dispatch('setLowerStakes', lowerStakesWEI)

  let upperAllStakesWEIBigNumber = new BigNumber(store.state.ECBcontractInstance().GetAllBetsUpper(store.state.web3.betKey, {from: store.state.web3.coinbase})[0])
  let upperAllStakesWEI = upperAllStakesWEIBigNumber.toNumber()
  // console.log('upperAllStakesWEI', upperAllStakesWEI)
  store.dispatch('setUpperAllStakes', upperAllStakesWEI)

  let lowerAllStakesWEIBigNumber = new BigNumber(store.state.ECBcontractInstance().GetAllBetsLower(store.state.web3.betKey, {from: store.state.web3.coinbase})[0])
  let lowerAllStakesWEI = lowerAllStakesWEIBigNumber.toNumber()
  // console.log('lowerAllStakesWEI', lowerAllStakesWEI)
  store.dispatch('setLowerAllStakes', lowerAllStakesWEI)
}

let updateState = async function () {
  let web3 = window.web3
  let _true = true
  web3 = new Web3(new Web3.providers.HttpProvider(SelectedProvider))

  console.log('web3.currentProvider', web3.currentProvider)

  // console.log('web3 && store.state.web3.web3Instance', web3, '&&', store.state.web3.web3Instance)
  if (_true /* web3 && store.state.web3.web3Instance */) {
    console.log('web3.eth.coinbase !== store.state.web3.coinbase', store.state.web3.coinbase)
    // if (web3.eth.coinbase !== store.state.web3.coinbase)
    if (_true) {
      console.log('web3.eth.coinbase !== store.state.web3.coinbase',
        // 'web3.eth.coinbase', web3.eth.coinbase,
        'store.state.web3.coinbase=', store.state.web3.coinbase,
        'web3=', web3)

      let newCoinbase = store.state.web3.coinbase
      let newAccountInput = store.state.web3.account_input
      web3.eth.getBalance(newCoinbase, function (err, newBalance) {
        if (err) {
          console.log(err)
        } else {
          let newBalanceInt = BigNumber(newBalance.toString(10)).toNumber()
          store.dispatch('pollWeb3', {
            coinbase: newCoinbase,
            balance: toFixedSpecial(newBalanceInt, 0),
            account_input: newAccountInput // changed
          })
        }
      })
      console.log('store.state.web3.coinbase', store.state.web3.coinbase)
      let GTABallanceBigNumber = new BigNumber(store.state.GTAcontractInstance().balanceOf(store.state.web3.coinbase))
      let GTABallanceWEI = GTABallanceBigNumber.toNumber()
      store.dispatch('setGTAbalance', GTABallanceWEI)
      let BetGTABallanceBigNumber = new BigNumber(store.state.ECBcontractInstance().GetCurrentDepo({from: store.state.web3.coinbase}))
      let BetGTABallanceWEI = BetGTABallanceBigNumber.toNumber()
      store.dispatch('setBetGTAbalance', BetGTABallanceWEI)
      let GTAtoECBallowanceWEIBigNumber = new BigNumber(store.state.GTAcontractInstance().allowance(store.state.web3.coinbase, store.state.ECBcontractInstance().address))
      let GTAtoECBallowanceWEI = GTAtoECBallowanceWEIBigNumber.toNumber()
      console.log('GTAtoECBallowanceWEI', GTAtoECBallowanceWEI)
      store.dispatch('setGTAtoECBallowance', GTAtoECBallowanceWEI)
    } /* else {
      web3.eth.getBalance(store.state.web3.coinbase, (err, polledBalance) => {
        if (err) {
          console.log(err)
        } else if (parseInt(polledBalance, 10) !== store.state.web3.balance) {
          store.dispatch('pollWeb3', {
            coinbase: store.state.web3.coinbase,
            balance: polledBalance,
            account_input: store.state.web3.account_input
          })
        }
      })
    } */
  }
}

let pollWeb3 = function () {
  updateState()
  updateEvents()
  updateBetState()
  updateAwardEvents()
  console.log('web3.currentProvider')
  setInterval(async () => { updateState() }, 20000) // 240 seconds 4 mins
  setInterval(async () => { updateEvents() }, 15000) // 15 seconds
  setInterval(async () => { updateBetState() }, 10000) // 10 seconds
  setInterval(async () => { updateAwardEvents() }, 15000) // 30 seconds
}

// export default pollWeb3
export { pollWeb3, updateState, updateEvents, updateBetState, updateAwardEvents }



